
import { defineComponent } from "vue";

export default defineComponent({
    name: "GridCell",
    props: {
        index: {
            type: Number,
            required: true,
        },
        isAlive: {
            type: Boolean,
            required: true,
        },
    },
    methods: {
        onMouseEvent(e: MouseEvent) {
            if (e.buttons == 1) {
                this.$emit("toggled", this.index);
            }
        },
    },
    emits: ["toggled"],
});
